.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-header {
  margin: 0 auto;
  padding-top: 43px;
  padding-left: 120px;
  padding-right: 120px;
  max-width: 1440px;
  background-color: #3172a8;
  background-image: url("../img/background-ad-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover; }

.main-header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .main-header {
    padding-top: 36px;
    padding-left: 109px;
    padding-right: 112px; }
  .main-header__top {
    display: flex;
    justify-content: space-between; }
  .main-header__toggle {
    position: relative;
    margin-top: 10px;
    display: block;
    width: 23px;
    height: 16px;
    background: none;
    border: none;
    cursor: pointer; }
    .main-header__toggle::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 24px;
      height: 2px;
      background-color: #ffffff;
      box-shadow: 0 14px 0 0 #ffffff;
      content: ""; }
    .main-header__toggle::after {
      position: absolute;
      top: 7px;
      left: 0;
      display: block;
      width: 15px;
      height: 2px;
      background-color: #ffffff;
      content: ""; } }

@media (max-width: 768px) {
  .main-header {
    background-image: url("../img/background-ad-tablet@1x.jpg");
    background-size: auto; } }

@media (max-width: 767px) {
  .main-header {
    padding-top: 13px;
    padding-left: 20px;
    padding-right: 20px; }
  .main-header__toggle {
    margin-top: 22px; } }

@media (max-width: 320px) {
  .main-header {
    background-image: url("../img/background-ad-mobile@1x.jpg");
    background-size: auto; } }

.main-navigation {
  padding-left: 2px;
  padding-right: 1px;
  display: flex;
  font-family: "Montserrat", "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 25px;
  color: #ffffff; }

.main-navigation__logo {
  margin-right: 84px; }

.main-navigation__phone {
  padding-top: 23px;
  min-width: 165px;
  font-weight: bold;
  font-size: 19px;
  line-height: 30px;
  color: #ffffff;
  text-decoration: none;
  text-align: right; }
  .main-navigation__phone:hover, .main-navigation__phone:focus {
    opacity: 0.7; }

@media (max-width: 1023px) {
  .main-navigation__phone {
    padding-top: 7px;
    font-size: 16px;
    line-height: 25px; }
  .main-navigation__logo {
    margin-right: 26px; } }

@media (max-width: 767px) {
  .main-navigation {
    flex-direction: column; }
  .main-navigation__logo {
    margin-left: auto;
    margin-right: 0; }
  .main-navigation__phone {
    margin-top: -3px;
    padding-top: 0;
    min-width: 115px;
    font-size: 14px;
    line-height: 22px; } }

.site-navigation {
  margin: 0;
  margin-right: auto;
  padding: 0;
  padding-top: 23px;
  display: flex;
  flex-wrap: wrap;
  list-style: none; }

.site-navigation__item {
  margin-right: 47px;
  margin-bottom: 15px; }

.site-navigation__link {
  color: #ffffff;
  text-decoration: none; }
  .site-navigation__link:hover, .site-navigation__link:focus {
    opacity: 0.7; }

@media (max-width: 1023px) {
  .site-navigation {
    display: none; } }

.advertisement {
  margin: 0 auto;
  padding-top: 151px;
  padding-bottom: 120px;
  max-width: 1440px;
  font-family: "Montserrat", "Open Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 19px;
  line-height: 28px;
  color: #ffffff; }

.advertisement h1 {
  position: relative;
  margin-bottom: 9px;
  font-weight: 800;
  font-size: 45px;
  line-height: 55px; }
  .advertisement h1::before {
    position: absolute;
    top: -46px;
    left: 1px;
    display: block;
    width: 100%;
    height: 115px;
    font-weight: 900;
    font-size: 93px;
    line-height: 113px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: #f5f9fc;
    opacity: 0.2;
    content: "Trekking"; }

.advertisement p {
  margin: 0;
  margin-bottom: 44px;
  padding-left: 2px;
  max-width: 470px; }

.advertisement__button {
  margin-bottom: 110px;
  padding: 17px 0;
  display: block;
  width: 248px;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 25px;
  color: #ffffff;
  text-decoration: none;
  background-color: #0266c1; }
  .advertisement__button:hover, .advertisement__button:focus {
    opacity: 0.7; }

.social-list {
  margin: 0;
  margin-right: -14px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none; }

.social-list__item {
  margin-right: 17px; }
  .social-list__item--vk {
    margin-left: auto; }

.social-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 21px;
  height: 21px;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(2, 102, 193, 0.43); }
  .social-list__link:hover, .social-list__link:focus {
    opacity: 0.7; }

@media (max-width: 1023px) {
  .advertisement {
    padding-top: 97px; }
  .advertisement h1 {
    margin-bottom: 14px;
    max-width: 300px;
    font-size: 35px;
    line-height: 43px; }
    .advertisement h1::before {
      position: absolute;
